	import {mapMutations} from 'vuex';
	import {authLogin, loginByPass, loginBySmsCode, smsCode} from "@/api/login";
	import {websocketUrl} from "../../api/params";
	const graceChecker = require("@/common/graceChecker.js");
	export default{
		data(){
			return {
				mobile: '',
				password: '',
				code: '',
				logining: false,
				loginByPass: true,
				smsCodeBtnDisabled: false,
				codeSeconds: 60,
				userInfo: null
			}
		},
		onLoad(options){
			this.userInfo = options.userInfo;
      // uni.clearStorageSync();
		},
		methods: {
	    loginTest(mobile, password) {
	        this.mobile = mobile;
	        this.password = password;
	    },
			...mapMutations(['login']),
			// 验证手机号是否有效
			checkPhoneIsValid (mobile) {
				const reg = /^1[0-9]{10,10}$/;
				if (!reg.test(mobile)) {
					this.$api.msg('请输入正确的手机号');
					return false;
				} else {
					return true;
				}
			},
			// 发送验证码并进入倒计时
			getSmsCode () {
				if (!this.checkPhoneIsValid(this.mobile)) return;
				this.$post(smsCode, {
					mobile: this.mobile,
					usage: 'login'
				}).then(r=>{
				  this.$api.msg(`验证码发送成功, 验证码是${r.data}`, 3 * 1000);
					this.smsCodeBtnDisabled = true;
					let time = 59;
					let timer = setInterval(() => {
						if(time === 0) {
							clearInterval(timer);
							this.smsCodeBtnDisabled = false;
						} else {
							this.codeSeconds = time;
							this.smsCodeBtnDisabled = true;
							time--
						 }
					},1000);
				})
			},
			// 失去焦点的手机号
			blurMobileChange(e) {
				this.mobile = e.detail.value
			},
			// 切换登录方式
			showLoginBySmsCode () {
				this.loginByPass = !this.loginByPass;
			},
			// 返回上一页
			navBack(){
				uni.navigateBack();
			},
			// 统一跳转路由
			navTo (url) {
				uni.navigateTo({
					url
				})
			},
			// 返回主页
			toHome(){
				uni.switchTab({
					url: '/pages/index/index'
				})
			},
			// 登录事件
			async toLogin(e){
				const formData = e.detail.value;
				const params = {};
				params.mobile = formData.mobile;
				let rule = [
					{name:"mobile", checkType : "phoneno", checkRule:"11,11",  errorMsg:"请输入正确的手机号"}
				];
				if (this.loginByPass) {
					params.password = formData.password;
          rule[1] = {name:"password", checkType : "string", checkRule:"6,20",  errorMsg:"密码长度为6-20位"};
				} else {
					params.code = formData.code;
          rule[1] = {name:"code", checkType : "notnull", checkRule:"",  errorMsg:"请输入验证码"};
				}
				const checkRes = graceChecker.check(formData, rule);
				if(!checkRes){
					this.$api.msg(graceChecker.error);
					return;
				}







				params.group = 'tinyShopWechatMq'




				if (this.loginByPass) {
					this.handleLoginByPass(params)
				} else {
					this.handleLoginBySmsCode(params)
				}
			},
			// 密码登录
			handleLoginByPass (params) {







				params.group = 'tinyShopWechatMq'




				this.$post(loginByPass, {
					...params
				}).then(r=>{
					this.$api.msg('恭喜您，登录成功！');
					this.login(r.data);
					this.initWebsocket();
					if (this.userInfo) {
						const oauthClientParams = {}

						oauthClientParams.oauth_client = 'wechatMp';




						const userInfo = JSON.parse(this.userInfo);
						this.$post(authLogin, {
							...userInfo,
							...oauthClientParams,
							gender: userInfo.sex || userInfo.gender,
							oauth_client_user_id: userInfo.openid || userInfo.openId,
							head_portrait: userInfo.headimgurl || userInfo.avatarUrl,
						});
					}
					uni.reLaunch({
						url: '/pages/user/user'
					});
				});
			},
			// 验证码登录
			handleLoginBySmsCode (params) {







				params.group = 'tinyShopWechatMq'




				this.$post(loginBySmsCode, {
					...params
				}).then(r=>{
					this.initWebsocket();
			    this.$api.msg('恭喜您，登录成功！')
					this.login(r.data);
					uni.switchTab({
						url: '/pages/user/user'
					})
				})
			},
			initWebsocket() {
				let websocketTimer = null;
        uni.connectSocket({url: websocketUrl});
        uni.onSocketOpen(() => {
          uni.setStorageSync('socketOpen', true);
				  this.websocketSend(JSON.stringify({"route": "site.login", "token": uni.getStorageSync('accessToken')}));
				  websocketTimer = setInterval(() => {
					  this.websocketSend(JSON.stringify({"route": "site.ping"}));
          }, 60 * 1000);
          console.log('websocket 连接成功')
        });
        uni.onSocketError(function (res) {
        	clearInterval(websocketTimer);
          uni.connectSocket({url: websocketUrl});
          console.log('WebSocket连接打开失败，请检查！', res);
        });
      },
			websocketSend(data) {
				uni.sendSocketMessage({data});
			},
		}
	}
